// Breakpoints
$smallDesktopBreakpoint: 1200px;
$tabletBreakpoint: 900px;
$mobileBreakpoint: 600px;

// Colors
$dark-shade: #1f2041;

$dark-shade-75: rgba($dark-shade, 0.75);
$dark-shade-50: rgba($dark-shade, 0.5);
$dark-shade-25: rgba($dark-shade, 0.25);
$dark-shade-10: rgba($dark-shade, 0.1);
$dark-shade-5: rgba($dark-shade, 0.05);

$prime-color: #bc9cff;
$sub-prime-color: #8ba4f9;
$second-color: #6fcf97;

$gradient-excellent: linear-gradient(180deg, #ffe39c 0%, #ffba9c 100%);
$gradient-good: linear-gradient(180deg, #6fcf97 0%, #66d2ea 100%);
$gradient-satisfied: linear-gradient(180deg, $prime-color 0%, $sub-prime-color 100%);
$gradient-disappointed: linear-gradient(180deg, #919191 0%, #3d4975 100%);

// Fonts sizes
$fs-h1: 1.7143rem;
$lh-h1: 2.1429rem;

$fs-h2: 1.3571rem;
$lh-h2: 1.7142rem;

$fs-h3: 0.8571rem;
$lh-h3: 1.0714rem;

$fs-body: 1rem;
$lh-body: 1.7143rem;

@mixin container {
  width: 100%;
  min-width: 320px;
  max-width: 1190px;
  margin: 0 auto;
  padding: 1.0714rem;
}

%mixin reset-link {
  text-decoration: none;

  &:active,
  &:hover,
  &:focus {
    text-decoration: none;
    outline: none;
  }

  &:visited {
    text-decoration: none;
  }
}

@mixin require-font($family, $dir, $font-weight: normal, $font-style: normal) {
  @font-face {
    font-family: "#{$family}";
    font-weight: $font-weight;
    font-style: $font-style;
    font-display: swap;
    src: url("@/chunks/fonts/#{$dir}/#{$dir}-#{$font-weight}-#{$font-style}.woff2") format("woff2"),
      url("@/chunks/fonts/#{$dir}/#{$dir}-#{$font-weight}-#{$font-style}.woff") format("woff");
  }
}

@mixin font-h1 {
  font-style: normal;
  font-variant: normal;
  font-weight: bold;
  font-size: $fs-h1;
  line-height: $lh-h1;
  font-family: Quicksand, Arial, sans-serif;
  color: $dark-shade;
}

@mixin font-h2 {
  font-style: normal;
  font-variant: normal;
  font-weight: bold;
  font-size: $fs-h2;
  line-height: $lh-h2;
  font-family: Quicksand, Arial, sans-serif;
  color: $dark-shade;
}

@mixin font-h3 {
  font-style: normal;
  font-variant: normal;
  font-weight: bold;
  font-size: $fs-h3;
  line-height: $lh-h3;
  font-family: Montserrat, Arial, sans-serif;
  color: $dark-shade;
  text-transform: uppercase;
}

@mixin font-body {
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  font-size: $fs-body;
  line-height: $lh-body;
  font-family: Montserrat, Arial, sans-serif;
  color: $dark-shade-75;
}

@mixin breakpoint($width) {
  @media screen and (max-width: $width) {
    @content;
  }
}

.room-reserve {
  max-width: 380px;
  padding: 40px 30px 30px 30px;

  border: 1px solid rgba(0, 0, 0, 0.12);
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  background-color: #fff;

  &__number {
    padding-bottom: 20px;
  }

  &__arrive {
    padding-bottom: 20px;
  }

  &__guest {
    padding-bottom: 21px;
  }

  &__summary {
    @include font-body;

    display: flex;
    justify-content: space-between;

    position: relative;
    padding-bottom: 20px;

    line-height: 1.285rem;
  }

  &__summary-item {
    display: block;
    max-width: 214px;
  }

  &__summary-mark {
    position: absolute;
    right: 80px;
    top: 0;

    height: 20px;
    width: 20px;
    border: 1px solid $dark-shade-25;

    border-radius: 50%;
    text-align: center;
    color: $dark-shade-25;
    font-weight: bold;
    cursor: help;
  }

  &__total {
    @include font-h2;

    display: flex;
    justify-content: space-between;
    padding: 20px 0;

    white-space: nowrap;
  }

  &__total-line {
    border-bottom: 1px dotted $dark-shade-25;
    width: 100%;
    height: 20px;
  }
}
