// Breakpoints
$smallDesktopBreakpoint: 1200px;
$tabletBreakpoint: 900px;
$mobileBreakpoint: 600px;

// Colors
$dark-shade: #1f2041;

$dark-shade-75: rgba($dark-shade, 0.75);
$dark-shade-50: rgba($dark-shade, 0.5);
$dark-shade-25: rgba($dark-shade, 0.25);
$dark-shade-10: rgba($dark-shade, 0.1);
$dark-shade-5: rgba($dark-shade, 0.05);

$prime-color: #bc9cff;
$sub-prime-color: #8ba4f9;
$second-color: #6fcf97;

$gradient-excellent: linear-gradient(180deg, #ffe39c 0%, #ffba9c 100%);
$gradient-good: linear-gradient(180deg, #6fcf97 0%, #66d2ea 100%);
$gradient-satisfied: linear-gradient(180deg, $prime-color 0%, $sub-prime-color 100%);
$gradient-disappointed: linear-gradient(180deg, #919191 0%, #3d4975 100%);

// Fonts sizes
$fs-h1: 1.7143rem;
$lh-h1: 2.1429rem;

$fs-h2: 1.3571rem;
$lh-h2: 1.7142rem;

$fs-h3: 0.8571rem;
$lh-h3: 1.0714rem;

$fs-body: 1rem;
$lh-body: 1.7143rem;

@mixin container {
  width: 100%;
  min-width: 320px;
  max-width: 1190px;
  margin: 0 auto;
  padding: 1.0714rem;
}

%mixin reset-link {
  text-decoration: none;

  &:active,
  &:hover,
  &:focus {
    text-decoration: none;
    outline: none;
  }

  &:visited {
    text-decoration: none;
  }
}

@mixin require-font($family, $dir, $font-weight: normal, $font-style: normal) {
  @font-face {
    font-family: "#{$family}";
    font-weight: $font-weight;
    font-style: $font-style;
    font-display: swap;
    src: url("@/chunks/fonts/#{$dir}/#{$dir}-#{$font-weight}-#{$font-style}.woff2") format("woff2"),
      url("@/chunks/fonts/#{$dir}/#{$dir}-#{$font-weight}-#{$font-style}.woff") format("woff");
  }
}

@mixin font-h1 {
  font-style: normal;
  font-variant: normal;
  font-weight: bold;
  font-size: $fs-h1;
  line-height: $lh-h1;
  font-family: Quicksand, Arial, sans-serif;
  color: $dark-shade;
}

@mixin font-h2 {
  font-style: normal;
  font-variant: normal;
  font-weight: bold;
  font-size: $fs-h2;
  line-height: $lh-h2;
  font-family: Quicksand, Arial, sans-serif;
  color: $dark-shade;
}

@mixin font-h3 {
  font-style: normal;
  font-variant: normal;
  font-weight: bold;
  font-size: $fs-h3;
  line-height: $lh-h3;
  font-family: Montserrat, Arial, sans-serif;
  color: $dark-shade;
  text-transform: uppercase;
}

@mixin font-body {
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  font-size: $fs-body;
  line-height: $lh-body;
  font-family: Montserrat, Arial, sans-serif;
  color: $dark-shade-75;
}

@mixin breakpoint($width) {
  @media screen and (max-width: $width) {
    @content;
  }
}

.input {
  @include font-body;
  margin: 0;
  width: 100%;
  max-width: 320px;

  $switch: &;

  &_view-hover {
    #{$switch}__field {
      &::placeholder {
        color: $dark-shade-50;
      }
    }
  }

  &__title {
    padding-bottom: 7px;
  }

  &__field {
    @include font-body;
    border: 1px solid $dark-shade-25;
    width: 100%;
    height: 44px;
    border-radius: 4px;
    padding: 7px 14px 8px;
    outline: none;

    &::placeholder {
      color: $dark-shade-25;
    }

    &:hover,
    &:focus {
      border: 1px solid $dark-shade-50;
      &::placeholder {
        color: $dark-shade-50;
      }
    }
  }

  &__container_view-arrow {
    position: relative;
  }

  &_view-arrow {
    #{$switch}__field {
      padding: 8px 37px 7px 15px;
      cursor: pointer;

      &_svg {
        position: absolute;
        bottom: 12px;
        right: 12px;
        height: 18px;
        width: 18px;
        cursor: pointer;
      }
    }
  }

  &_view-masked {
    #{$switch}__field {
      cursor: not-allowed;

      &:hover,
      &:focus {
        border: 1px solid $dark-shade-25;
        &::placeholder {
          color: $dark-shade-25;
        }
      }

      &:disabled {
        background: #fff;
      }
    }
  }

  &_with-filter-date {
    position: relative;

    #{$switch}__field {
      padding-right: 40px;
      cursor: pointer;
    }

    #{$switch}__field-wrapper {
      position: relative;

      &::after {
        content: "";
        display: inline-block;
        position: absolute;
        bottom: 13px;
        right: 18px;
        width: 10px;
        height: 10px;
        border: 2px solid $dark-shade-50;
        border-top: 0;
        border-left: 0;
        transform: translateY(-50%) rotate(45deg);
        cursor: pointer;
      }
    }
  }

  &__container_view-range {
    flex-basis: 150px;
  }

  &_view-range {
    display: flex;
    position: relative;
    justify-content: space-between;

    #{$switch}__field {
      padding-right: 40px;
      cursor: pointer;
      flex-basis: 150px;
    }

    #{$switch}__field-wrapper {
      position: relative;

      &::after {
        content: "";
        display: inline-block;
        position: absolute;
        bottom: 13px;
        right: 18px;
        width: 10px;
        height: 10px;
        border: 2px solid $dark-shade-50;
        border-top: 0;
        border-left: 0;
        transform: translateY(-50%) rotate(45deg);
        cursor: pointer;
      }
    }
  }
}
