// Breakpoints
$smallDesktopBreakpoint: 1200px;
$tabletBreakpoint: 900px;
$mobileBreakpoint: 600px;

// Colors
$dark-shade: #1f2041;

$dark-shade-75: rgba($dark-shade, 0.75);
$dark-shade-50: rgba($dark-shade, 0.5);
$dark-shade-25: rgba($dark-shade, 0.25);
$dark-shade-10: rgba($dark-shade, 0.1);
$dark-shade-5: rgba($dark-shade, 0.05);

$prime-color: #bc9cff;
$sub-prime-color: #8ba4f9;
$second-color: #6fcf97;

$gradient-excellent: linear-gradient(180deg, #ffe39c 0%, #ffba9c 100%);
$gradient-good: linear-gradient(180deg, #6fcf97 0%, #66d2ea 100%);
$gradient-satisfied: linear-gradient(180deg, $prime-color 0%, $sub-prime-color 100%);
$gradient-disappointed: linear-gradient(180deg, #919191 0%, #3d4975 100%);

// Fonts sizes
$fs-h1: 1.7143rem;
$lh-h1: 2.1429rem;

$fs-h2: 1.3571rem;
$lh-h2: 1.7142rem;

$fs-h3: 0.8571rem;
$lh-h3: 1.0714rem;

$fs-body: 1rem;
$lh-body: 1.7143rem;

@mixin container {
  width: 100%;
  min-width: 320px;
  max-width: 1190px;
  margin: 0 auto;
  padding: 1.0714rem;
}

%mixin reset-link {
  text-decoration: none;

  &:active,
  &:hover,
  &:focus {
    text-decoration: none;
    outline: none;
  }

  &:visited {
    text-decoration: none;
  }
}

@mixin require-font($family, $dir, $font-weight: normal, $font-style: normal) {
  @font-face {
    font-family: "#{$family}";
    font-weight: $font-weight;
    font-style: $font-style;
    font-display: swap;
    src: url("@/chunks/fonts/#{$dir}/#{$dir}-#{$font-weight}-#{$font-style}.woff2") format("woff2"),
      url("@/chunks/fonts/#{$dir}/#{$dir}-#{$font-weight}-#{$font-style}.woff") format("woff");
  }
}

@mixin font-h1 {
  font-style: normal;
  font-variant: normal;
  font-weight: bold;
  font-size: $fs-h1;
  line-height: $lh-h1;
  font-family: Quicksand, Arial, sans-serif;
  color: $dark-shade;
}

@mixin font-h2 {
  font-style: normal;
  font-variant: normal;
  font-weight: bold;
  font-size: $fs-h2;
  line-height: $lh-h2;
  font-family: Quicksand, Arial, sans-serif;
  color: $dark-shade;
}

@mixin font-h3 {
  font-style: normal;
  font-variant: normal;
  font-weight: bold;
  font-size: $fs-h3;
  line-height: $lh-h3;
  font-family: Montserrat, Arial, sans-serif;
  color: $dark-shade;
  text-transform: uppercase;
}

@mixin font-body {
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  font-size: $fs-body;
  line-height: $lh-body;
  font-family: Montserrat, Arial, sans-serif;
  color: $dark-shade-75;
}

@mixin breakpoint($width) {
  @media screen and (max-width: $width) {
    @content;
  }
}

.pie-chart {
  $switch: &;

  position: relative;
  display: block;

  &__title {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: baseline;
    padding-bottom: 23px;

    @include font-h2;

    &::after {
      content: attr(data-subtitle);
      @include font-h3;
      color: $dark-shade-50;
      font-weight: normal;
    }
  }

  &__container {
    position: relative;
    display: flex;
    justify-content: space-between;
    max-width: 311px;
  }

  &__graph {
    position: relative;
    width: 120px;
    height: 120px;
    align-self: center;
  }

  &__svg {
    position: absolute;
    transform: rotate(90deg);
  }

  &__circle {
    transition: 0.5s;

    &_hovered {
      stroke-width: 3;
      transition: 0.5s;
    }

    &:hover {
      stroke-width: 3;
      transition: 0.5s;
    }
  }

  &__legend {
    display: block;
    padding-left: 20px;
    align-self: flex-end;

    cursor: default;
    user-select: none;
  }

  &__item {
    @include font-body;

    display: block;
    position: relative;

    text-transform: none;

    &::before {
      content: "";

      position: absolute;
      margin: auto;
      top: 0;
      left: -15px;
      bottom: 0;

      width: 10px;
      height: 10px;

      border-radius: 50%;
    }

    &_theme_excellent {
      &::before {
        background: $gradient-excellent;
      }
    }

    &_theme_good {
      &::before {
        background: $gradient-good;
      }
    }

    &_theme_satisfied {
      &::before {
        background: $gradient-satisfied;
      }
    }

    &_theme_disappointed {
      &::before {
        background: $gradient-disappointed;
      }
    }

    &:hover {
      color: $dark-shade-50;

      + #{$switch}__votes {
        opacity: 1;
        transition: 0.2s;
      }
    }
  }

  &__votes {
    @include font-h3;
    position: absolute;
    opacity: 0;

    margin: auto;
    top: $lh-h1;
    left: 28px;
    bottom: 0;

    text-transform: uppercase;
    height: min-content;
    width: min-content;
    transition: 0.5s;

    &::before {
      @include font-h1;

      content: attr(data-votes);
      position: absolute;

      margin: auto;
      left: 0;
      bottom: $lh-h1 + $lh-h3;
      right: 0;
      top: 0;

      height: min-content;
      width: min-content;
    }

    &_visible {
      opacity: 1;
      transition: 0.2s;
    }

    &_theme_excellent {
      background: $gradient-excellent;
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;

      &::before {
        background: $gradient-excellent;
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
    }

    &_theme_good {
      background: $gradient-good;
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;

      &::before {
        background: $gradient-good;
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
    }

    &_theme_satisfied {
      background: $gradient-satisfied;
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;

      &::before {
        background: $gradient-satisfied;
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
    }

    &_theme_disappointed {
      background: $gradient-disappointed;
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;

      &::before {
        background: $gradient-disappointed;
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
    }
  }

  &__total {
    @include font-h3;
    position: absolute;

    margin: auto;
    top: $lh-h1;
    left: 28px;
    bottom: 0;

    text-transform: uppercase;
    height: min-content;
    width: min-content;

    transition: 0.5s;
    opacity: 1;

    &_invisible {
      transition: 0.2s;
      opacity: 0;
    }

    &::before {
      @include font-h1;

      content: attr(data-votes);
      position: absolute;

      margin: auto;
      left: 0;
      bottom: $lh-h1 + $lh-h3;
      right: 0;
      top: 0;

      height: min-content;
      width: min-content;
    }
  }
}
