// Breakpoints
$smallDesktopBreakpoint: 1200px;
$tabletBreakpoint: 900px;
$mobileBreakpoint: 600px;

// Colors
$dark-shade: #1f2041;

$dark-shade-75: rgba($dark-shade, 0.75);
$dark-shade-50: rgba($dark-shade, 0.5);
$dark-shade-25: rgba($dark-shade, 0.25);
$dark-shade-10: rgba($dark-shade, 0.1);
$dark-shade-5: rgba($dark-shade, 0.05);

$prime-color: #bc9cff;
$sub-prime-color: #8ba4f9;
$second-color: #6fcf97;

$gradient-excellent: linear-gradient(180deg, #ffe39c 0%, #ffba9c 100%);
$gradient-good: linear-gradient(180deg, #6fcf97 0%, #66d2ea 100%);
$gradient-satisfied: linear-gradient(180deg, $prime-color 0%, $sub-prime-color 100%);
$gradient-disappointed: linear-gradient(180deg, #919191 0%, #3d4975 100%);

// Fonts sizes
$fs-h1: 1.7143rem;
$lh-h1: 2.1429rem;

$fs-h2: 1.3571rem;
$lh-h2: 1.7142rem;

$fs-h3: 0.8571rem;
$lh-h3: 1.0714rem;

$fs-body: 1rem;
$lh-body: 1.7143rem;

@mixin container {
  width: 100%;
  min-width: 320px;
  max-width: 1190px;
  margin: 0 auto;
  padding: 1.0714rem;
}

%mixin reset-link {
  text-decoration: none;

  &:active,
  &:hover,
  &:focus {
    text-decoration: none;
    outline: none;
  }

  &:visited {
    text-decoration: none;
  }
}

@mixin require-font($family, $dir, $font-weight: normal, $font-style: normal) {
  @font-face {
    font-family: "#{$family}";
    font-weight: $font-weight;
    font-style: $font-style;
    font-display: swap;
    src: url("@/chunks/fonts/#{$dir}/#{$dir}-#{$font-weight}-#{$font-style}.woff2") format("woff2"),
      url("@/chunks/fonts/#{$dir}/#{$dir}-#{$font-weight}-#{$font-style}.woff") format("woff");
  }
}

@mixin font-h1 {
  font-style: normal;
  font-variant: normal;
  font-weight: bold;
  font-size: $fs-h1;
  line-height: $lh-h1;
  font-family: Quicksand, Arial, sans-serif;
  color: $dark-shade;
}

@mixin font-h2 {
  font-style: normal;
  font-variant: normal;
  font-weight: bold;
  font-size: $fs-h2;
  line-height: $lh-h2;
  font-family: Quicksand, Arial, sans-serif;
  color: $dark-shade;
}

@mixin font-h3 {
  font-style: normal;
  font-variant: normal;
  font-weight: bold;
  font-size: $fs-h3;
  line-height: $lh-h3;
  font-family: Montserrat, Arial, sans-serif;
  color: $dark-shade;
  text-transform: uppercase;
}

@mixin font-body {
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  font-size: $fs-body;
  line-height: $lh-body;
  font-family: Montserrat, Arial, sans-serif;
  color: $dark-shade-75;
}

@mixin breakpoint($width) {
  @media screen and (max-width: $width) {
    @content;
  }
}

.like {
  $switch: &;
  display: block;

  &:hover {
    filter: drop-shadow(0 0 2px $dark-shade-25);
  }

  &__label {
    @include font-h3;

    display: inline-block;
    position: relative;

    font-size: 10px;
    line-height: 12px;
    font-weight: normal;
    color: $dark-shade-50;

    cursor: pointer;
  }

  &__box {
    display: inline-block;
    padding: 3px 7px 3px 22px;

    min-width: 40px;

    border-radius: 10px;
    border: solid 1px transparent;

    background-color: #fff;
    background-clip: padding-box;

    &::before {
      content: "";
      position: absolute;
      z-index: -1;
      border-radius: 10px;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      background-color: $dark-shade-25;
    }

    &::after {
      content: url(../../components/like/images/favorite_border.svg);
      position: absolute;
      top: 5px;
      left: 5px;
    }
  }

  &__input {
    position: absolute;
    appearance: none;

    // checked
    &:checked {
      + #{$switch}__box {
        color: $prime-color;

        &::before {
          background: linear-gradient(
            180deg,
            $prime-color 0%,
            $sub-prime-color 100%
          );
        }

        &::after {
          content: url(../../components/like/images/favorite.svg);
        }
      }
    }

    // focused
    &:focus {
      + #{$switch}__box {
        &::before {
          background-color: $dark-shade-25;
          box-shadow: 0 0 2px 1px $dark-shade-10;
        }
      }
    }

    &:checked:focus {
      + #{$switch}__box {
        &::before {
          background: linear-gradient(
            180deg,
            $prime-color 0%,
            $sub-prime-color 100%
          );
          box-shadow: 0 0 2px 1px $dark-shade-10;
        }
      }
    }
  }
}
