// Breakpoints
$smallDesktopBreakpoint: 1200px;
$tabletBreakpoint: 900px;
$mobileBreakpoint: 600px;

// Colors
$dark-shade: #1f2041;

$dark-shade-75: rgba($dark-shade, 0.75);
$dark-shade-50: rgba($dark-shade, 0.5);
$dark-shade-25: rgba($dark-shade, 0.25);
$dark-shade-10: rgba($dark-shade, 0.1);
$dark-shade-5: rgba($dark-shade, 0.05);

$prime-color: #bc9cff;
$sub-prime-color: #8ba4f9;
$second-color: #6fcf97;

$gradient-excellent: linear-gradient(180deg, #ffe39c 0%, #ffba9c 100%);
$gradient-good: linear-gradient(180deg, #6fcf97 0%, #66d2ea 100%);
$gradient-satisfied: linear-gradient(180deg, $prime-color 0%, $sub-prime-color 100%);
$gradient-disappointed: linear-gradient(180deg, #919191 0%, #3d4975 100%);

// Fonts sizes
$fs-h1: 1.7143rem;
$lh-h1: 2.1429rem;

$fs-h2: 1.3571rem;
$lh-h2: 1.7142rem;

$fs-h3: 0.8571rem;
$lh-h3: 1.0714rem;

$fs-body: 1rem;
$lh-body: 1.7143rem;

@mixin container {
  width: 100%;
  min-width: 320px;
  max-width: 1190px;
  margin: 0 auto;
  padding: 1.0714rem;
}

%mixin reset-link {
  text-decoration: none;

  &:active,
  &:hover,
  &:focus {
    text-decoration: none;
    outline: none;
  }

  &:visited {
    text-decoration: none;
  }
}

@mixin require-font($family, $dir, $font-weight: normal, $font-style: normal) {
  @font-face {
    font-family: "#{$family}";
    font-weight: $font-weight;
    font-style: $font-style;
    font-display: swap;
    src: url("@/chunks/fonts/#{$dir}/#{$dir}-#{$font-weight}-#{$font-style}.woff2") format("woff2"),
      url("@/chunks/fonts/#{$dir}/#{$dir}-#{$font-weight}-#{$font-style}.woff") format("woff");
  }
}

@mixin font-h1 {
  font-style: normal;
  font-variant: normal;
  font-weight: bold;
  font-size: $fs-h1;
  line-height: $lh-h1;
  font-family: Quicksand, Arial, sans-serif;
  color: $dark-shade;
}

@mixin font-h2 {
  font-style: normal;
  font-variant: normal;
  font-weight: bold;
  font-size: $fs-h2;
  line-height: $lh-h2;
  font-family: Quicksand, Arial, sans-serif;
  color: $dark-shade;
}

@mixin font-h3 {
  font-style: normal;
  font-variant: normal;
  font-weight: bold;
  font-size: $fs-h3;
  line-height: $lh-h3;
  font-family: Montserrat, Arial, sans-serif;
  color: $dark-shade;
  text-transform: uppercase;
}

@mixin font-body {
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  font-size: $fs-body;
  line-height: $lh-body;
  font-family: Montserrat, Arial, sans-serif;
  color: $dark-shade-75;
}

@mixin breakpoint($width) {
  @media screen and (max-width: $width) {
    @content;
  }
}

.card-slider {
  $switch: &;

  &__carousel {
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
    border-top-right-radius: 4px;
    border-top-left-radius: 4px;

    &:focus-within {
      box-shadow: 0px 5px 5px #1f20414d;
    }
  }

  &__input {
    position: absolute;
    width: 1px;
    height: 1px;
    overflow: hidden;
    clip: rect(0 0 0 0);

    &:focus ~ .card-slider__dots {
      filter: drop-shadow(0px 0px 1px #000);
    }
  }

  &__controls {
    display: none;
  }

  // special thanks to https://codepen.io/jh3y/pen/WwVKLN?editors=1100
  @for $num from 1 through 10 {
    &__input:nth-of-type(#{$num}) {
      &:checked ~ .card-slider__track {
        transform: translateX(-(($num - 1) * 100%));
      }

      &:checked ~ .card-slider__controls:nth-of-type(#{$num}) {
        display: block;
        opacity: 1;
      }

      &:checked ~ .card-slider__dots .card-slider__dot:nth-of-type(#{$num}) {
        background-color: #fff;
      }
    }
  }

  // Create style for slider button
  &__control {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    width: 40px;
    height: 100%;
    z-index: 3;
    opacity: 0;

    &::before {
      content: "";
      position: absolute;
      margin: auto;
      right: 20px;
      top: 0;
      bottom: 0;
      height: 10px;
      width: 10px;
      border: 2px solid #fff;
      border-top: 0;
      border-left: 0;
      transform: rotate(135deg);
      z-index: 3;
    }

    &_prev {
      left: 0;
      background: #000;
      background: linear-gradient(
        90deg,
        rgba(0, 0, 0, 0.5) 0%,
        rgba(0, 0, 0, 0) 99.99%,
        rgba(255, 255, 255, 0) 100%
      );
      z-index: 4;
    }

    &_next {
      right: 0;
      background: linear-gradient(
        90deg,
        rgba(0, 0, 0, 0.5) 0%,
        rgba(0, 0, 0, 0) 99.99%,
        rgba(255, 255, 255, 0) 100%
      );
      transform: rotate(-180deg);
      z-index: 4;
    }

    &:hover {
      opacity: 1;
      cursor: pointer;
      transition: 0.5s;
    }
  }

  // Create rules for  a track
  &__track {
    display: flex;
    transition: 0.5s;
  }

  &__slide {
    flex: 0 0 100%;
    width: 100%;

    @for $num from 1 through 10 {
      &:nth-of-type(#{$num}) {
        transform: translateX(($num - 1) * 100%);
      }
    }

    // Create style for arrow on a link-img
    &:hover::after {
      @include font-h1;
      content: attr(data-subtitle);
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      color: #fff;
      opacity: 0.7;
      line-height: 1rem;
      filter: drop-shadow(0px 0px 1px #1f20414d);
    }
  }

  &__img {
    display: block;
    width: 100%;
    height: auto;
  }

  &__dots {
    position: absolute;
    bottom: 15px;
    right: 15px;
  }

  &__dot {
    height: 8px;
    width: 8px;
    border-radius: 100%;
    display: inline-block;
    z-index: 2;
    cursor: pointer;
    margin: 0 0 0 3.75px;
    border: 1px solid #fff;
    box-shadow: 0px 5px 5px #1f20414d;
  }
}
