// Breakpoints
$smallDesktopBreakpoint: 1200px;
$tabletBreakpoint: 900px;
$mobileBreakpoint: 600px;

// Colors
$dark-shade: #1f2041;

$dark-shade-75: rgba($dark-shade, 0.75);
$dark-shade-50: rgba($dark-shade, 0.5);
$dark-shade-25: rgba($dark-shade, 0.25);
$dark-shade-10: rgba($dark-shade, 0.1);
$dark-shade-5: rgba($dark-shade, 0.05);

$prime-color: #bc9cff;
$sub-prime-color: #8ba4f9;
$second-color: #6fcf97;

$gradient-excellent: linear-gradient(180deg, #ffe39c 0%, #ffba9c 100%);
$gradient-good: linear-gradient(180deg, #6fcf97 0%, #66d2ea 100%);
$gradient-satisfied: linear-gradient(180deg, $prime-color 0%, $sub-prime-color 100%);
$gradient-disappointed: linear-gradient(180deg, #919191 0%, #3d4975 100%);

// Fonts sizes
$fs-h1: 1.7143rem;
$lh-h1: 2.1429rem;

$fs-h2: 1.3571rem;
$lh-h2: 1.7142rem;

$fs-h3: 0.8571rem;
$lh-h3: 1.0714rem;

$fs-body: 1rem;
$lh-body: 1.7143rem;

@mixin container {
  width: 100%;
  min-width: 320px;
  max-width: 1190px;
  margin: 0 auto;
  padding: 1.0714rem;
}

%mixin reset-link {
  text-decoration: none;

  &:active,
  &:hover,
  &:focus {
    text-decoration: none;
    outline: none;
  }

  &:visited {
    text-decoration: none;
  }
}

@mixin require-font($family, $dir, $font-weight: normal, $font-style: normal) {
  @font-face {
    font-family: "#{$family}";
    font-weight: $font-weight;
    font-style: $font-style;
    font-display: swap;
    src: url("@/chunks/fonts/#{$dir}/#{$dir}-#{$font-weight}-#{$font-style}.woff2") format("woff2"),
      url("@/chunks/fonts/#{$dir}/#{$dir}-#{$font-weight}-#{$font-style}.woff") format("woff");
  }
}

@mixin font-h1 {
  font-style: normal;
  font-variant: normal;
  font-weight: bold;
  font-size: $fs-h1;
  line-height: $lh-h1;
  font-family: Quicksand, Arial, sans-serif;
  color: $dark-shade;
}

@mixin font-h2 {
  font-style: normal;
  font-variant: normal;
  font-weight: bold;
  font-size: $fs-h2;
  line-height: $lh-h2;
  font-family: Quicksand, Arial, sans-serif;
  color: $dark-shade;
}

@mixin font-h3 {
  font-style: normal;
  font-variant: normal;
  font-weight: bold;
  font-size: $fs-h3;
  line-height: $lh-h3;
  font-family: Montserrat, Arial, sans-serif;
  color: $dark-shade;
  text-transform: uppercase;
}

@mixin font-body {
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  font-size: $fs-body;
  line-height: $lh-body;
  font-family: Montserrat, Arial, sans-serif;
  color: $dark-shade-75;
}

@mixin breakpoint($width) {
  @media screen and (max-width: $width) {
    @content;
  }
}

.pagination {
  @include font-body;

  text-align: center;
  position: relative;

  &__pages {
    display: flex;
    justify-content: center;
  }

  &__page {
    cursor: pointer;
    display: block;
    outline: none;

    color: $dark-shade-50;
    line-height: 40px;
    text-align: center;
    text-decoration: none;

    border-radius: 20px;
    width: 40px;
    height: 40px;

    &:focus {
      filter: drop-shadow(0 0 2px $dark-shade-50);
    }

    &:active {
      color: $dark-shade;
    }

    &:hover:not(.pagination__page_current):not(.pagination__page_next):not(.pagination__page_skip) {
      background: linear-gradient(
        180deg,
        lighten($prime-color, 10%) 0%,
        lighten($sub-prime-color, 10%) 100%
      );
      color: #fff;
    }

    // &:visited {

    // }

    &_current {
      background: linear-gradient(
        180deg,
        $prime-color 0%,
        $sub-prime-color 100%
      );
      font-weight: bold;
      color: #fff;
    }

    &_skip {
      cursor: default;
    }

    &_next {
      position: relative;
      display: block;
      background: linear-gradient(180deg, $second-color 0%, #66d2ea 100%);

      &::after {
        content: url(../../components/pagination/images/arrow.svg);
        position: absolute;
        margin: auto;

        top: 0;
        bottom: 0;
        left: 0;
        right: 0;

        width: min-content;
        height: min-content;
      }

      &:hover {
        background: linear-gradient(
          180deg,
          lighten($second-color, 10%) 0%,
          lighten(#66d2ea, 10%) 100%
        );
      }
    }
  }

  &__total {
    padding-top: 11px;
    text-transform: none;
    color: $dark-shade-75;
  }
}
