// Breakpoints
$smallDesktopBreakpoint: 1200px;
$tabletBreakpoint: 900px;
$mobileBreakpoint: 600px;

// Colors
$dark-shade: #1f2041;

$dark-shade-75: rgba($dark-shade, 0.75);
$dark-shade-50: rgba($dark-shade, 0.5);
$dark-shade-25: rgba($dark-shade, 0.25);
$dark-shade-10: rgba($dark-shade, 0.1);
$dark-shade-5: rgba($dark-shade, 0.05);

$prime-color: #bc9cff;
$sub-prime-color: #8ba4f9;
$second-color: #6fcf97;

$gradient-excellent: linear-gradient(180deg, #ffe39c 0%, #ffba9c 100%);
$gradient-good: linear-gradient(180deg, #6fcf97 0%, #66d2ea 100%);
$gradient-satisfied: linear-gradient(180deg, $prime-color 0%, $sub-prime-color 100%);
$gradient-disappointed: linear-gradient(180deg, #919191 0%, #3d4975 100%);

// Fonts sizes
$fs-h1: 1.7143rem;
$lh-h1: 2.1429rem;

$fs-h2: 1.3571rem;
$lh-h2: 1.7142rem;

$fs-h3: 0.8571rem;
$lh-h3: 1.0714rem;

$fs-body: 1rem;
$lh-body: 1.7143rem;

@mixin container {
  width: 100%;
  min-width: 320px;
  max-width: 1190px;
  margin: 0 auto;
  padding: 1.0714rem;
}

%mixin reset-link {
  text-decoration: none;

  &:active,
  &:hover,
  &:focus {
    text-decoration: none;
    outline: none;
  }

  &:visited {
    text-decoration: none;
  }
}

@mixin require-font($family, $dir, $font-weight: normal, $font-style: normal) {
  @font-face {
    font-family: "#{$family}";
    font-weight: $font-weight;
    font-style: $font-style;
    font-display: swap;
    src: url("@/chunks/fonts/#{$dir}/#{$dir}-#{$font-weight}-#{$font-style}.woff2") format("woff2"),
      url("@/chunks/fonts/#{$dir}/#{$dir}-#{$font-weight}-#{$font-style}.woff") format("woff");
  }
}

@mixin font-h1 {
  font-style: normal;
  font-variant: normal;
  font-weight: bold;
  font-size: $fs-h1;
  line-height: $lh-h1;
  font-family: Quicksand, Arial, sans-serif;
  color: $dark-shade;
}

@mixin font-h2 {
  font-style: normal;
  font-variant: normal;
  font-weight: bold;
  font-size: $fs-h2;
  line-height: $lh-h2;
  font-family: Quicksand, Arial, sans-serif;
  color: $dark-shade;
}

@mixin font-h3 {
  font-style: normal;
  font-variant: normal;
  font-weight: bold;
  font-size: $fs-h3;
  line-height: $lh-h3;
  font-family: Montserrat, Arial, sans-serif;
  color: $dark-shade;
  text-transform: uppercase;
}

@mixin font-body {
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  font-size: $fs-body;
  line-height: $lh-body;
  font-family: Montserrat, Arial, sans-serif;
  color: $dark-shade-75;
}

@mixin breakpoint($width) {
  @media screen and (max-width: $width) {
    @content;
  }
}

.header {
  $switch: &;

  &__main {
    box-shadow: 0px 10px 20px $dark-shade-5;
    background-color: #fff;
    margin: 0 auto;
    padding: 15px 0;
  }

  &__container {
    @include container;
    @include font-body;
    padding-top: 0;
    padding-bottom: 0;

    position: relative;

    display: flex;
    justify-content: space-between;
    flex-flow: row wrap;

    @include breakpoint($smallDesktopBreakpoint) {
      flex-direction: column;
      align-items: center;
    }

    @include breakpoint($tabletBreakpoint) {
      align-items: stretch;
    }
  }

  &__logo {
    width: fit-content;
    display: block;
    outline: none;

    &:focus-within,
    &:hover {
      filter: drop-shadow(0px 0px 5px $sub-prime-color);
    }
  }

  &__nav {
    display: flex;
    flex-flow: row wrap;

    @include breakpoint($tabletBreakpoint) {
      display: none;
    }

    &_is-open {
      @include breakpoint($tabletBreakpoint) {
        display: block;
      }
    }
  }

  &__menu {
    display: flex;
    align-items: center;

    @include breakpoint($smallDesktopBreakpoint) {
      &_with-buttons {
        justify-content: center;
      }
    }

    @include breakpoint($tabletBreakpoint) {
      padding-top: 10px;
      flex-direction: column;

      &_with-buttons {
        flex-direction: row;
        justify-content: space-between;
      }
    }
  }

  &__item {
    display: inline-block;
    margin-right: 20px;

    @include breakpoint($tabletBreakpoint) {
      margin: 0;
    }

    &_with-menu {
      &::after {
        content: "";
        display: inline-block;
        width: 8px;
        height: 8px;

        margin-left: 9px;
        margin-bottom: -2px;

        border: 2px solid $dark-shade-50;
        border-top: 0;
        border-left: 0;
        transform: translateY(-50%) rotate(45deg);
      }

      &:hover::after {
        border: 2px solid $dark-shade-25;
        border-top: 0;
        border-left: 0;
      }
    }

    &_bold {
      font-weight: bold;

      > #{$switch}__link {
        color: $dark-shade-75;
      }
    }
  }

  &__link {
    text-decoration: none;
    color: $dark-shade-50;

    cursor: pointer;
    outline: none;

    &:hover {
      color: $dark-shade-25;
    }

    &:focus {
      filter: drop-shadow(1px 1px 5px $sub-prime-color);
    }
  }

  &__user {
    border-left: 1px solid $dark-shade-10;
    padding: 6px 0 7px 29px;

    @include breakpoint($tabletBreakpoint) {
      border: 0;
      border-top: 1px solid $dark-shade-10;
      padding: 3px 0 0 0;
    }
  }

  // special thanks to https://codepen.io/Zaku/pen/vcaFr
  &__toggle {
    $easeOutBack: cubic-bezier(0.25, -0.25, 0.75, 1.25);
    $easing: $easeOutBack;
    $duration: 0.5s;

    display: none;
    position: absolute;
    top: 5px;
    right: 15px;
    width: 35px;
    outline: none;

    @include breakpoint($tabletBreakpoint) {
      display: block;
    }

    & svg {
      cursor: pointer;
      transform: translate3d(0, 0, 0);
    }

    & path {
      fill: none;
      transition: stroke-dashoffset $duration $easing, stroke-dasharray $duration $easing;
      stroke-width: 40px;
      stroke-linecap: round;
      stroke: $dark-shade;
      stroke-dashoffset: 0px;

      &#top,
      &#bottom {
        stroke-dasharray: 240px 1000px;
      }

      &#middle {
        stroke-dasharray: 240px 240px;
      }
    }

    &_active {
      path {
        &#top,
        &#bottom {
          stroke-dashoffset: -650px;
          stroke-dashoffset: -650px;
        }

        &#middle {
          stroke-dashoffset: -115px;
          stroke-dasharray: 1px 220px;
        }
      }
    }

    &:focus,
    &:hover {
      filter: drop-shadow(0px 0px 5px $sub-prime-color);
    }
  }
}
