// Breakpoints
$smallDesktopBreakpoint: 1200px;
$tabletBreakpoint: 900px;
$mobileBreakpoint: 600px;

// Colors
$dark-shade: #1f2041;

$dark-shade-75: rgba($dark-shade, 0.75);
$dark-shade-50: rgba($dark-shade, 0.5);
$dark-shade-25: rgba($dark-shade, 0.25);
$dark-shade-10: rgba($dark-shade, 0.1);
$dark-shade-5: rgba($dark-shade, 0.05);

$prime-color: #bc9cff;
$sub-prime-color: #8ba4f9;
$second-color: #6fcf97;

$gradient-excellent: linear-gradient(180deg, #ffe39c 0%, #ffba9c 100%);
$gradient-good: linear-gradient(180deg, #6fcf97 0%, #66d2ea 100%);
$gradient-satisfied: linear-gradient(180deg, $prime-color 0%, $sub-prime-color 100%);
$gradient-disappointed: linear-gradient(180deg, #919191 0%, #3d4975 100%);

// Fonts sizes
$fs-h1: 1.7143rem;
$lh-h1: 2.1429rem;

$fs-h2: 1.3571rem;
$lh-h2: 1.7142rem;

$fs-h3: 0.8571rem;
$lh-h3: 1.0714rem;

$fs-body: 1rem;
$lh-body: 1.7143rem;

@mixin container {
  width: 100%;
  min-width: 320px;
  max-width: 1190px;
  margin: 0 auto;
  padding: 1.0714rem;
}

%mixin reset-link {
  text-decoration: none;

  &:active,
  &:hover,
  &:focus {
    text-decoration: none;
    outline: none;
  }

  &:visited {
    text-decoration: none;
  }
}

@mixin require-font($family, $dir, $font-weight: normal, $font-style: normal) {
  @font-face {
    font-family: "#{$family}";
    font-weight: $font-weight;
    font-style: $font-style;
    font-display: swap;
    src: url("@/chunks/fonts/#{$dir}/#{$dir}-#{$font-weight}-#{$font-style}.woff2") format("woff2"),
      url("@/chunks/fonts/#{$dir}/#{$dir}-#{$font-weight}-#{$font-style}.woff") format("woff");
  }
}

@mixin font-h1 {
  font-style: normal;
  font-variant: normal;
  font-weight: bold;
  font-size: $fs-h1;
  line-height: $lh-h1;
  font-family: Quicksand, Arial, sans-serif;
  color: $dark-shade;
}

@mixin font-h2 {
  font-style: normal;
  font-variant: normal;
  font-weight: bold;
  font-size: $fs-h2;
  line-height: $lh-h2;
  font-family: Quicksand, Arial, sans-serif;
  color: $dark-shade;
}

@mixin font-h3 {
  font-style: normal;
  font-variant: normal;
  font-weight: bold;
  font-size: $fs-h3;
  line-height: $lh-h3;
  font-family: Montserrat, Arial, sans-serif;
  color: $dark-shade;
  text-transform: uppercase;
}

@mixin font-body {
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  font-size: $fs-body;
  line-height: $lh-body;
  font-family: Montserrat, Arial, sans-serif;
  color: $dark-shade-75;
}

@mixin breakpoint($width) {
  @media screen and (max-width: $width) {
    @content;
  }
}

.rating {
  $switch: &;
  $width: 24;
  $height: 19;
  padding: 2px 0 2px 2px;
  border: 0;

  &__group {
    position: relative;
    width: #{$width * 5}px;
    height: #{$height}px;
    background-image: url(../../components/rating/images/off.svg);
    background-size: #{$width}px #{$height}px;
    background-repeat: repeat-x;

    &:focus-within {
      filter: drop-shadow(0 0 2px $dark-shade-25);
    }

    &:hover {
      filter: drop-shadow(0 0 2px $dark-shade-25);
    }
  }

  &__star {
    cursor: pointer;
    position: absolute;
    appearance: none;
    top: 0;
    left: 0;
    height: #{$height}px;
    margin: 0;
    background-size: #{$width}px #{$height}px;
    background-repeat: repeat-x;

    &:focus {
      outline: none;
    }

    &:checked,
    &:hover {
      background-image: url(../../components/rating/images/on.svg);
      background-size: #{$width}px #{$height}px;
    }

    &:hover ~ .rating__star {
      background-image: url(../../components/rating/images/off.svg);
    }

    // stars from 1 to 5
    &:nth-of-type(1) {
      z-index: 5;
      width: #{$width * 1}px;
    }

    &:nth-of-type(2) {
      z-index: 4;
      width: #{$width * 2}px;
    }

    &:nth-of-type(3) {
      z-index: 3;
      width: #{$width * 3}px;
    }

    &:nth-of-type(4) {
      z-index: 2;
      width: #{$width * 4}px;
    }

    &:nth-of-type(5) {
      z-index: 1;
      width: #{$width * 5}px;
    }
  }
}
