// Breakpoints
$smallDesktopBreakpoint: 1200px;
$tabletBreakpoint: 900px;
$mobileBreakpoint: 600px;

// Colors
$dark-shade: #1f2041;

$dark-shade-75: rgba($dark-shade, 0.75);
$dark-shade-50: rgba($dark-shade, 0.5);
$dark-shade-25: rgba($dark-shade, 0.25);
$dark-shade-10: rgba($dark-shade, 0.1);
$dark-shade-5: rgba($dark-shade, 0.05);

$prime-color: #bc9cff;
$sub-prime-color: #8ba4f9;
$second-color: #6fcf97;

$gradient-excellent: linear-gradient(180deg, #ffe39c 0%, #ffba9c 100%);
$gradient-good: linear-gradient(180deg, #6fcf97 0%, #66d2ea 100%);
$gradient-satisfied: linear-gradient(180deg, $prime-color 0%, $sub-prime-color 100%);
$gradient-disappointed: linear-gradient(180deg, #919191 0%, #3d4975 100%);

// Fonts sizes
$fs-h1: 1.7143rem;
$lh-h1: 2.1429rem;

$fs-h2: 1.3571rem;
$lh-h2: 1.7142rem;

$fs-h3: 0.8571rem;
$lh-h3: 1.0714rem;

$fs-body: 1rem;
$lh-body: 1.7143rem;

@mixin container {
  width: 100%;
  min-width: 320px;
  max-width: 1190px;
  margin: 0 auto;
  padding: 1.0714rem;
}

%mixin reset-link {
  text-decoration: none;

  &:active,
  &:hover,
  &:focus {
    text-decoration: none;
    outline: none;
  }

  &:visited {
    text-decoration: none;
  }
}

@mixin require-font($family, $dir, $font-weight: normal, $font-style: normal) {
  @font-face {
    font-family: "#{$family}";
    font-weight: $font-weight;
    font-style: $font-style;
    font-display: swap;
    src: url("@/chunks/fonts/#{$dir}/#{$dir}-#{$font-weight}-#{$font-style}.woff2") format("woff2"),
      url("@/chunks/fonts/#{$dir}/#{$dir}-#{$font-weight}-#{$font-style}.woff") format("woff");
  }
}

@mixin font-h1 {
  font-style: normal;
  font-variant: normal;
  font-weight: bold;
  font-size: $fs-h1;
  line-height: $lh-h1;
  font-family: Quicksand, Arial, sans-serif;
  color: $dark-shade;
}

@mixin font-h2 {
  font-style: normal;
  font-variant: normal;
  font-weight: bold;
  font-size: $fs-h2;
  line-height: $lh-h2;
  font-family: Quicksand, Arial, sans-serif;
  color: $dark-shade;
}

@mixin font-h3 {
  font-style: normal;
  font-variant: normal;
  font-weight: bold;
  font-size: $fs-h3;
  line-height: $lh-h3;
  font-family: Montserrat, Arial, sans-serif;
  color: $dark-shade;
  text-transform: uppercase;
}

@mixin font-body {
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  font-size: $fs-body;
  line-height: $lh-body;
  font-family: Montserrat, Arial, sans-serif;
  color: $dark-shade-75;
}

@mixin breakpoint($width) {
  @media screen and (max-width: $width) {
    @content;
  }
}

.datepicker-inline {
  display: block;
  position: absolute;
  top: 81px;

  .datepicker {
    position: absolute;
    display: block;
    width: 320px;
    padding: 18px;
    background: #fff;
    border: 1px solid $dark-shade-25;
    box-sizing: border-box;
    box-shadow: 0px 10px 20px $dark-shade-5;
    border-radius: 4px;

    &--nav {
      border-bottom: 0;
      padding: 0;
      min-height: 24px;

      &-title {
        @include font-h2;

        i {
          color: inherit;
        }
      }

      &-action {
        width: 24px;
        height: 24px;

        & path {
          fill: $prime-color;
          stroke: none;
        }

        & svg {
          width: 18px;
          height: 18px;
        }
      }
    }

    &--content {
      padding: 30px 0 20px;
    }

    &--days {
      &-names {
        margin-top: 0;
      }
    }

    &--day {
      &-name {
        @include font-h3;

        height: 40px;

        color: $prime-color;
        text-transform: none;
      }
    }

    &--cell {
      &-day {
        font: 12px Montserrat, Arial, sans-serif;
        height: 40px;
        border-radius: 50%;
        color: $dark-shade-50;

        &.-disabled- {
          color: $dark-shade-25;
        }

        &.-current- {
          color: white;
          font-weight: bold;
          background: $gradient-good;
        }

        &.-selected- {
          font-weight: bold;
          background: linear-gradient(
            180deg,
            $prime-color 0%,
            $sub-prime-color 100%
          );
          color: white;
        }

        &.-other-month- {
          color: $dark-shade-25;
        }

        &.-range-from-::after,
        &.-range-to-::after {
          content: "";
          position: absolute;
          left: 0;
          right: 0;
          bottom: 0;
          top: 0;
          background: linear-gradient(
            180deg,
            rgba(188, 156, 255, 0.25),
            rgba(139, 164, 249, 0.25)
          );
          z-index: -1;
        }

        &.-range {
          &-from- {
            border: 0;

            &::after {
              border-radius: 50% 0 0 50%;
            }
          }
          &-to- {
            border: 0;

            &::after {
              border-radius: 0 50% 50% 0;
            }
          }
        }

        &.-in-range- {
          background: linear-gradient(
            180deg,
            rgba(188, 156, 255, 0.25),
            rgba(139, 164, 249, 0.25)
          );
          color: $dark-shade-50;
          border-radius: 0;
        }
      }
    }

    &--buttons {
      border-top: 0;
      padding: 0;
      display: flex;
      justify-content: space-between;
    }

    &--button {
      flex: none;
      font-size: 12px;
      line-height: 15px;
      text-transform: uppercase;
      font-family: montserrat, sans-serif;
      font-weight: 700;
      color: $dark-shade-50;

      &[data-action="today"] {
        color: $prime-color;
      }

      &:hover {
        color: lighten($prime-color, 10%);
        background: 0;
      }
    }
  }
}
