// Breakpoints
$smallDesktopBreakpoint: 1200px;
$tabletBreakpoint: 900px;
$mobileBreakpoint: 600px;

// Colors
$dark-shade: #1f2041;

$dark-shade-75: rgba($dark-shade, 0.75);
$dark-shade-50: rgba($dark-shade, 0.5);
$dark-shade-25: rgba($dark-shade, 0.25);
$dark-shade-10: rgba($dark-shade, 0.1);
$dark-shade-5: rgba($dark-shade, 0.05);

$prime-color: #bc9cff;
$sub-prime-color: #8ba4f9;
$second-color: #6fcf97;

$gradient-excellent: linear-gradient(180deg, #ffe39c 0%, #ffba9c 100%);
$gradient-good: linear-gradient(180deg, #6fcf97 0%, #66d2ea 100%);
$gradient-satisfied: linear-gradient(180deg, $prime-color 0%, $sub-prime-color 100%);
$gradient-disappointed: linear-gradient(180deg, #919191 0%, #3d4975 100%);

// Fonts sizes
$fs-h1: 1.7143rem;
$lh-h1: 2.1429rem;

$fs-h2: 1.3571rem;
$lh-h2: 1.7142rem;

$fs-h3: 0.8571rem;
$lh-h3: 1.0714rem;

$fs-body: 1rem;
$lh-body: 1.7143rem;

@mixin container {
  width: 100%;
  min-width: 320px;
  max-width: 1190px;
  margin: 0 auto;
  padding: 1.0714rem;
}

%mixin reset-link {
  text-decoration: none;

  &:active,
  &:hover,
  &:focus {
    text-decoration: none;
    outline: none;
  }

  &:visited {
    text-decoration: none;
  }
}

@mixin require-font($family, $dir, $font-weight: normal, $font-style: normal) {
  @font-face {
    font-family: "#{$family}";
    font-weight: $font-weight;
    font-style: $font-style;
    font-display: swap;
    src: url("@/chunks/fonts/#{$dir}/#{$dir}-#{$font-weight}-#{$font-style}.woff2") format("woff2"),
      url("@/chunks/fonts/#{$dir}/#{$dir}-#{$font-weight}-#{$font-style}.woff") format("woff");
  }
}

@mixin font-h1 {
  font-style: normal;
  font-variant: normal;
  font-weight: bold;
  font-size: $fs-h1;
  line-height: $lh-h1;
  font-family: Quicksand, Arial, sans-serif;
  color: $dark-shade;
}

@mixin font-h2 {
  font-style: normal;
  font-variant: normal;
  font-weight: bold;
  font-size: $fs-h2;
  line-height: $lh-h2;
  font-family: Quicksand, Arial, sans-serif;
  color: $dark-shade;
}

@mixin font-h3 {
  font-style: normal;
  font-variant: normal;
  font-weight: bold;
  font-size: $fs-h3;
  line-height: $lh-h3;
  font-family: Montserrat, Arial, sans-serif;
  color: $dark-shade;
  text-transform: uppercase;
}

@mixin font-body {
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  font-size: $fs-body;
  line-height: $lh-body;
  font-family: Montserrat, Arial, sans-serif;
  color: $dark-shade-75;
}

@mixin breakpoint($width) {
  @media screen and (max-width: $width) {
    @content;
  }
}

.checkbox-list {
  position: relative;
  $switch: &;

  &__toggle {
    appearance: none;
    position: absolute;
    right: 1px;
    top: 1px;

    &:checked {
      ~ #{$switch}__items {
        display: none;
      }

      ~ #{$switch}__title {
        #{$switch}__mark {
          transform: translateY(-50%) rotate(45deg);
        }
      }
    }

    &:focus {
      ~ #{$switch}__title {
        #{$switch}__mark {
          filter: drop-shadow(0 0 2px $dark-shade-75);
        }
      }
    }
  }

  &__title {
    cursor: pointer;
  }

  &__mark {
    display: inline-block;
    position: absolute;
    right: 1px;
    top: 5px;
    width: 10px;
    height: 10px;
    border: 2px solid $dark-shade-50;
    border-top: 0;
    transform: translateY(0%) rotate(-135deg);
    border-left: 0;
    transition: 0.3s;
  }

  &__item {
    display: block;
    padding-bottom: 10px;

    &:first-child {
      padding-top: 17px;
    }

    &:last-child {
      padding-bottom: 0;
    }
  }
}
